/* General Styles */
body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: 'Arial', sans-serif;
    background: #f0f2f5;

  }
  
  .login-container {
    display: flex;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;

    background-color: rgb(99, 133, 142, 0.926);
    
   
  }
  
  .form-container1 {
    display: flex;
    height: 80%;
    width: 80%;
    max-width: 1200px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    border-radius: 15px;
   
  }
  
  .form-content {
    width: 50%;
    padding: 50px;
    background-color: #ffffff;
    background-image: url();
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    border-radius: 1rem;
    margin-left: 280px;
    
  }
  
  .image-container {
    width: 50%;
    position: relative;
    overflow: hidden;
  }
  
  .image-container .background-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .form-title {
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 30px;
    color: #333;
    text-align: center;
  }
  
  .form-item {
    margin-bottom: 20px;
  }
  
  .input-field {
    width: 100%;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
  }

  .button-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
  }
  
  .submit-button {
    width: 100%;
    padding: 12px;
    font-size: 16px;
    background-color: #007bff;
    border: none;
    color: white;
    border-radius: 5px;
    transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .submit-button:hover {
    background-color: #0056b3;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }
  
  .register-link {
    display: block;
    text-align: center;
    padding: 10px 20px;
    background-color: #28a745;
    color: white;
    border-radius: 5px;
    text-decoration: none;
    font-size: 16px;
    transition: background 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .register-link:hover {
    background-color: #218838;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  
  @media (max-width: 768px) {
    .form-container {
      flex-direction: column;
      width: 90%;
      height: auto;
    }
  
    .image-container {
      display: none;
    }
  
    .form-content {
      width: 100%;
    }
  }
  