.main {
  padding: 10px;
  height: 100vh;
  width: 98%;
}
.layout {
  
  display: flex;
  padding: .5rem .5rem;
  width: 100%;
  
}

.sidebar {
  min-height: 100%;
  width: 400px;
  border-radius: 10px;
  background-color: rgba(54, 88, 98, 0.926);
  box-shadow: 0 0 2px rgb(255, 245, 245);
  margin-right: 20px;
  
}

.content {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.header {
  height: 10vh;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: white;
}
.body {
  height: 100vh;
  margin-bottom: 2px;
  box-shadow: 0 0 2px gray;
  background-color: rgb(187, 249, 202);
  border-radius: 10px;
  overflow: auto;
}
.logo h6 {
  font-size: 1.8rem;
  text-align: center;
  margin: 20px 0px;
  color: rgba(19, 21, 49, 0.784);
  font-weight: 700;
  
}
.menu {
  margin-top: 50px;
}
.menu-item {
  margin-top: 30px;
}
.menu-item a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-size: 1.2rem;
}
.menu-item i {
  font-size: 1.2rem;
  margin: 0 15px;
  color: black;
}
.active {
  background-color: #e2f5e9;
  padding: 10px;
}
.active a,
.active i {
  color: rgb(0, 0, 0);
}

.header-content {
  display: flex;
  align-items: center;
  height: 50px;
  justify-content: flex-end;
  margin: 0 15px;
}

.header-content i {
  margin-right: 20px;
  font-size: 1.2rem;
}
.header-content a {
  text-decoration: none;
  font-size: 1.2rem;
  color: blue;
  margin: 0 10px;
  text-transform: uppercase;
}