* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  /* loader */
  .spinner {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.383);
  }


  
.dashboard-container {
  border-radius: 3rem;
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow:auto;
  padding: 1rem;
  
}

.user-con {
  height: 100px;
  display: flex;
  align-items: center;
  gap: 1rem;
  img {
    width: 135px;
    height: 135px;
    margin-left:85px;

    border-radius: 50%;
    object-fit: cover;
    background: #fcf6f9;
    border: 2px solid #f90000;
    padding: 0.2rem;
    
    box-shadow: 0px 1px 17px rgba(0, 0, 0, 0.06);
  }}

 .input-control input{
  border: 2px solid black;
 }

 .hh11{
  display: flex;
  align-items: center;
  color: #000000;
  margin-left: 500px;
 }

 .form-containter{
  overflow: auto;
 }