.stats-con{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
    .chart-con{
        grid-column: 1 / 4;
        height: 350px;
        .amount-con{
            
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 1rem;
            margin-top: 1rem;
            .income, .expense{
                grid-column: span 2;
            }
            .income, .expense, .balance{
                width :250px;
                height: 140px;
               
                background: #FCF6F9;
                border: 2px solid #FFFFFF;
                box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.06);
                border-radius: 20px;
                padding: 1rem;
                p{
                    font-size: 1.2rem;
                    font-weight: 700;
                    padding-left: 60px;
                    color: rgb(238, 156, 156);

               
                }
            }

            .balance{
                grid-column: 2 / 4;
                display: flex;
                flex-direction: column;
                
                width :250px;
                height: 130px;
                p{
                    color: var(--color-green);
                    opacity: 0.6;
                    font-size: 1.2rem;
                   
                }
            }
        }
    }

    .history-con{
        flex: 1;
        grid-column: 4 / -1;
        h2{
            margin: .5rem 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 1.5rem;
           
        }
        .salary-title{
            font-size: 1.2rem;
            span{
                font-size: 1.8rem;
            }
        }
        .salary-item{
            background: #FCF6F9;
            border: 2px solid #FFFFFF;
            box-shadow: 0px 1px 15px rgba(0, 0, 0, 0.06);
            padding: 1rem;
            border-radius: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p{
                font-weight: 600;
                font-size: 1.6rem;
                
            }
        }
    }
}


