:root {
    --primary-color: #4CAF50;
    --secondary-color: #FF9800;
    --background-color: #F4F4F9;
    --text-color: #333;
    --card-background: #FFF;
    --card-shadow: rgba(0, 0, 0, 0.1);
}

body {
    font-family: 'Arial', sans-serif;
    background-color: var(--background-color);
    margin: 0;
    padding: 0;
}

.home-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    height: 100vh;
    flex-wrap: wrap; /* Allow items to wrap on smaller screens */
}

.home-content,
.home-image {
    flex: 1;
    min-width: 300px; /* Ensure a minimum width for each flex item */
    margin: 1rem; /* Add margin for spacing */
}

.home-content {
    max-width: 50%;
    overflow: auto;
}

.home-content h1 {
    font-size: 3rem;
    color: var(--primary-color);
    margin-bottom: 1rem;
}

.home-content p {
    font-size: 1.2rem;
    color: var(--text-color);
    margin-bottom: 2rem;
}

.home-cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.5rem;
}

.home-card {
    background: var(--card-background);
    border-radius: 10px;
    padding: 1.5rem;
    box-shadow: 0 4px 6px var(--card-shadow);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.home-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 12px var(--card-shadow);
}

.home-card h2 {
    font-size: 1.5rem;
    color: var(--secondary-color);
    margin-bottom: 1rem;
}

.home-card p {
    font-size: 1rem;
    color: var(--text-color);
}

.home-image {
    position: relative;
    overflow: hidden;
    padding: 1rem;
    box-sizing: border-box;
}

.home-image img {
    width: 100%;
    border-radius: 10px;
    box-shadow: 0 4px 8px var(--card-shadow);
    transition: transform 0.3s ease, box-shadow 0.3s ease; /* Add transition for box-shadow */
}

.home-image img:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px var(--card-shadow); /* Increase shadow on hover */
}

/* Responsive styles */
@media (max-width: 650px) {
    .home-container {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 1rem;
    }

    .home-content,
    .home-image {
        max-width: 100%;
        text-align: center;
        padding: 1rem;
        margin: 0.5rem; /* Adjust margin for smaller screens */
    }

    .home-content h1 {
        font-size: 2rem;
    }

    .home-content p {
        font-size: 1rem;
    }

    .home-cards {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .home-card {
        padding: 1rem;
    }

    .home-card h2 {
        font-size: 1.25rem;
    }

    .home-card p {
        font-size: 0.9rem;
    }

    .home-image {
        padding: 1rem;
    }
}

@media (max-width: 480px) {
    .home-container {
        padding: 0.5rem;
    }

    .home-content,
    .home-image {
        padding: 0.5rem;
    }

    .home-content h1 {
        font-size: 1.5rem;
    }

    .home-content p {
        font-size: 0.9rem;
    }

    .home-cards {
        gap: 0.5rem;
    }

    .home-card {
        padding: 0.75rem;
    }

    .home-card h2 {
        font-size: 1rem;
    }

    .home-card p {
        font-size: 0.8rem;
    }

    .home-image {
        padding: 0.5rem;
    }
}
